@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400&family=Ubuntu:wght@300&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

.App {
  display: flex;
  flex-direction: column;
  width: 100%;
}









.headerContainer {
    display: flex;
    width: 100%;
    max-width: 1400px;
    margin: 0 auto;
    padding: 0 0px;
  }
  
  .logo img {
    padding: 20px 10px 10px 00px;
    width: 180px;
  }

  .navBar {
    display: flex;
    flex: 1 1;
    justify-content: space-between;
    align-items: center;
  }
  
  .navBar .search {
    border: solid 0.5px #0072ce;
    margin-left: 10px;
    display: flex;
    width: 70%;
    align-content: center;
  }
  
  .searchIconBG {
    background-color: #0072ce;
    color: #fff;
    padding: 8px 10px;
    border: solid 1px #0072ce;
  }
  
  .searchIcon {
    font-size: 18px;
  }
  
  input {
    border: 0px;
    outline: none;
    padding: 2px;
    width: 100%;
    padding-left: 10px;
    background-color: transparent;
    font-family: "Poppins", sans-serif;
  }
  
  .navBar ul {
    display: flex;
  }
  
  .navBar ul li {
    padding-right: 15px;
    list-style: none;
  }
 
  .navigationContainer {
    display: flex;
    width: 100%;
    max-width: 1400px;
    margin: 7px auto;
    padding: 0 0px;
    justify-content: space-between;
  }
  
  .primaryNavigation {
    display: flex;
    margin-top: 10px;
    /* justify-content: space-between; */
    /* margin: 10px 215px; */
  }
  
  .primaryNavigation ul {
    display: flex;
  }
  
  .primaryNavigation ul li {
    list-style-type: none;
    padding-right: 15px;
  }
  .primaryNavigation ul li a {
    text-decoration: none;
    font-size: 12px;
    font-weight: bold;
    color: #0072ce;
  }
  
  .primaryNavigation ul li a:hover {
    font-size: 12px;
    color: #0072ce;
    text-decoration: underline;
  }
  
  .departmentMenu {
    color: #fff !important;
    background-color: #0072ce;
    padding: 14px 20px 14px 40px;
    border-radius: 5px 5px 0 0;
    font-weight: bold;
    font-size: 14px !important;
    letter-spacing: 1px;
  }
  
  .departmentChevron {
    margin-left: 20px;
    font-size: 20px;
    padding-top: 8px;
  }
  
  hr {
    border: solid 1px #0072ce;
  }
  
  .secondaryNavigation {
    display: flex;
    justify-content: space-between;
  }
  
  .secondaryNavigation ul {
    display: flex;
  }
  
  .secondaryNavigation ul li {
    list-style-type: none;
    padding-right: 25px;
    font-size: 12px;
    margin-top: 10px;
  }
  
  .shopGrocery {
    font-size: 30px !important;
    color: #669530;
    padding-right: 5px;
  }
  
  .groceryDiv {
    color: #669530 !important;
    text-decoration: none !important;
    font-weight: bold;
    display: flex;
    align-items: center;
  }
  
  .storeFinderDiv {
    color: #0072ce !important;
    text-decoration: none !important;
    font-weight: bold;
    display: flex;
    align-items: center;
  }
  
  .storeFinder {
    font-size: 30px !important;
    color: #0072ce !important;
    padding-right: 5px;
  }
.homeContainer {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 1400px;
  margin: 7px auto;
  padding: 0 0px;
  justify-content: space-between;
}

.newToWalmart img {
  max-width: 100%;
}
.rollBackItems img {
  max-width: 100%;
}

.carousel {
  margin-top: 25px;
  max-width: 100%;
}

.carouselImage img {
  max-width: 100%;
}

.backToSchoolContainer {
  display: flex;  
  width: 100%;
  flex-direction: column;
  margin: 7px auto;
  padding: 0 0px;
  justify-content: space-between;
}

.backToSchoolHeading {
  max-width: 100%;
  display: grid;
  grid-template-columns: 1 1 1;
  margin-top: 50px;
  text-align: center;
  align-items: center;
}


.flyerLinkAndItems{
  display: flex;
  justify-content: space-between;
}

.spotlightContainer{
  display: flex;
  background-color: rgb(241, 241, 242);
  
  
}

.spotlightText{
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: left;
  padding: 25px ;
  height: 75%;
}


.spotlightText h4{
  font-weight: normal;
}

.spotlightText button{
  margin-top: 30px;
  border-radius: 20px;
  width: 40%;
  font-weight: bold;
  padding: 10px;
}

.spotlightText button:hover{
  background-color: darkgreen;
  cursor: pointer;
  color: black;
}


.backToSchoolContentContainer {
  display: flex;  
  margin-top: 20px;
}

.backToSchoolContentItems {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  margin-bottom: 10px;
  margin: 0 20px;
  /* width: 140px; */
  text-align: center;
  height: 100%;
}

.backToSchoolContentItems h6{
  font-size: 11px;
  font-weight: normal;
  color: #0072ce;
  padding-top: 15px;
  padding-bottom: 10px;
}

.backToSchoolContentItems h4{
  font-size: 17px;
  font-weight: bold;
  color: #0072ce;
}

.backToSchoolContentItems img {
  border-radius: 50%;
  padding: 10px;
  height: 130px;
  width: 130px;
}

.moreWaysContentContainer {
display: flex;  
margin-top: 20px;
}

.moreWaysContentItems {
display: flex;
flex-direction: column;
justify-content: space-evenly;
align-items: center;
margin-bottom: 10px;
margin: 0 20px;
width: 240px;
text-align: center;
height: 100%;
}

.moreWaysContentItems h4{
color: #0072ce;
padding-top: 15px;
padding-bottom: 10px;
}

.moreWaysContentItems img {
border-radius: 50%;
padding: 10px;
height: 180px;
width: 180px;
}

.moreWaysContentItems span{
font-size: 11px;   
}


.shopDepttContent{
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: center;
}

.shopDepttContentContainer{
    display: grid;
    width: 100%;    
    grid-template-columns: repeat(8, 1fr) ;
}

.shopDepttContentItems{
    max-width: 130px;
    text-align: center;
    height: 100%;
    margin: 20px 25px;
}

.shopDepttContentItems h6{
    color: #0072ce;
}
.shopDepttContentItems img {
    border-radius: 50%;
    padding: 5px;
    height: 130px;
    width: 130px;
  }
