.moreWaysContentContainer {
display: flex;  
margin-top: 20px;
}

.moreWaysContentItems {
display: flex;
flex-direction: column;
justify-content: space-evenly;
align-items: center;
margin-bottom: 10px;
margin: 0 20px;
width: 240px;
text-align: center;
height: 100%;
}

.moreWaysContentItems h4{
color: #0072ce;
padding-top: 15px;
padding-bottom: 10px;
}

.moreWaysContentItems img {
border-radius: 50%;
padding: 10px;
height: 180px;
width: 180px;
}

.moreWaysContentItems span{
font-size: 11px;   
}

